export default [
    {
        path: "salesmanList",
        name: "salesmanList",
        component: () => import("@/pages/salesmanManage/salesmanList/SalesmanList"),
        meta: ["推销员管理", "推销员列表"]
    },
    {
        path: "newsalesman",
        name: "newsalesman",
        component: () => import("@/pages/salesmanManage/newsalesman/Newsalesman"),
        meta: ["推销员管理", "新增推销员"]
    },
    {
        path: "editsalesman",
        name: "editsalesman",
        component: () => import("@/pages/salesmanManage/editsalesman/Editsalesman"),
        meta: ["推销员管理", "编辑推销员"]
    }
]


