import axios from 'axios'
import pubJson from './pubJson'
import qs from "qs"
import { Notification, MessageBox } from 'element-ui'
// 添加请求拦截器，在发送请求之前做些什么
axios.interceptors.request.use(function(config) {
    return config
}, function(error) {
    // 请求错误时弹框提示，或做些其他事
    return Promise.reject(error)
})

// 添加响应拦截器
axios.interceptors.response.use(function(response) {
    return response.data
}, function(error) {
    // 对响应错误做点什么
    return Promise.reject(error)
})

function errorState(response) {
    if (response && (response.status === 200)) {
        // 如果不需要除了data之外的数据，可以直接 return response.data
        return response.data
    } else {
        Notification.error({
            title: response
        })
    }
}

function successState(res) {
    if (res.code == '200' || res.rspCode == '200') {
        return res.data
    } else {
        throw new Error(res.msg);
    }
}

function apiAxios(method, url, params, headers) {
    if (method == "GET") {
        if (Object.prototype.toString.call(params) === "[object Array]") {
            url = url + "/" + params.join("/")
            params = null
        } else if (Object.prototype.toString.call(params) === '[object Object]') {
            // params={param:params}     
        }
    } else if (method === 'POST' || method === 'PUT') {
        if (headers && headers["Content-Type"] == "application/x-www-form-urlencodeds") {
            params = qs.stringify(params, { arrayFormat: 'indices' })
        }
    }
    let httpDefault = {
        method: method,
        baseURL: pubJson.url,
        url: url,
        headers: headers || {
            "Content-Type": "application/json;charset=UTF-8"
        },
        params: method === 'GET' || method === 'DELETE' ? params : null,
        data: method === 'POST' || method === 'PUT' ? params : null,
        timeout: 10000
    }
    let userInfo = JSON.parse(localStorage.getItem("userInfo"))
    if (userInfo && userInfo.jwtToken) httpDefault.headers.authorization = userInfo.jwtToken
    return new Promise((resolve, reject) => {
        axios(httpDefault)
            .then((res) => {
                let response = successState(res)
                resolve(response || {})
            }).catch((response) => {
                errorState(response)
                reject(response)
            })
    })
}

export default {
    install: function(Vue) {
        Vue.prototype.$get = (url, params, headers) => apiAxios('GET', url, params, headers)
        Vue.prototype.$post = (url, params, headers) => apiAxios('POST', url, params, headers)
        Vue.prototype.$put = (url, params, headers) => apiAxios('PUT', url, params, headers)
        Vue.prototype.$delect = (url, params, headers) => apiAxios('DELECT', url, params, headers)
    }
}


