export default [{
        path: "user/userList",
        component: () =>
            import ("@/pages/user/UserList"),
        meta: ["用户", "用户列表"]
    },
    {
        path: "user/addUser",
        component: () =>
            import ("@/pages/user/AddUser"),
        meta: ["用户", "新建用户"]
    },
    {
        path: "user/editUserInfo",
        component: () =>
            import ("@/pages/user/EditUserInfo"),
        meta: ["用户", "用户信息修改"]
    },
    {
        path: "user/structure",
        component: () =>
            import ("@/pages/user/Structure"),
        meta: ["用户", "组织架构"]
    },
    {
        path: "user/queryLogs",
        component: () =>
            import ("@/pages/user/QueryLogs"),
        meta: ["用户", "日志查询"]
    },


];


