<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  name: "App",
  created() {
    this.$router.replace("/page");
    return;
    //处理登录逻辑
    let userInfo = JSON.parse(localStorage.getItem("userInfo"));
    if (userInfo && userInfo.jwtToken) {
      this.$get("verifyUserStatus", { authorization: userInfo.jwtToken })
        .then(res => {
          localStorage.setItem("userInfo", JSON.stringify(res));
          if (this.$route.path == "/") this.$router.replace("/home");
        })
        .catch(res => {
          this.$router.replace("/");
          loading.close();
        });
    } else {
      this.$router.replace("/");
    }
  }
};
</script>

<style>

</style>



