import Vue from 'vue'
import Router from 'vue-router'
import pubJson from '../util/pubJson'
import pages from '@/pages/router'

Vue.use(Router)

export default new Router({
  mode: "hash",
  base: pubJson.package != "" ? "/" + pubJson.package + "/" : "",
  routes: [
    {
      path: '*',
      component: () => import("@/pages/html/NotFound"),
    },
    {
      path: '/',
      name: 'login',
      component: () => import("@/pages/html/login"),
      meta: { title: "登录" }
    },
    {
      path: '/home',
      name: 'home',
      component: () => import("@/pages/html/home"),
      meta: { title: "首页" }
    },
    {
      path: '/page',
      name: '页面',
      component: () => import("@/pages/html/page"),
      children: [...pages]
    },
  ]
})


