export default [
    {
        path: "shop/shopList",
        name: "shopList",
        component: () => import("@/pages/shop/shopList/ShopList"),
        meta: ["店铺管理", "店铺列表"]
    },
    {
        path: "shop/newShop",
        name: "newShop",
        component: () => import("@/pages/shop/newShop/NewShop"),
        meta: ["店铺管理", "新建店铺"]
    },
    {
        path: "shop/editShop",
        name: "editShop",
        component: () => import("@/pages/shop/editShop/EditShop"),
        meta: ["店铺管理", "编辑店铺"]
    },
    {
        path: "shop/changeManager",
        name: "changeManager",
        component: () => import("@/pages/shop/changeManager/ChangeManager"),
        meta: ["店铺管理", "更改店长"]
    }
]


