export default [
    {
        path: "stock/stockRecord",
        name: "stockRecord",
        component: () => import("@/pages/stock/stockRecord/StockRecord"),
        meta: ["库存管理" , "入库记录"]
    },
    {
        path: "stock/stockCheck",
        name: "stockCheck",
        component: () => import("@/pages/stock/stockCheck/StockCheck"),
        meta: ["库存管理" , "库存查看"]
    }
]


